//引入VueRouter
import VueRouter from 'vue-router'

//引入Luyou 组件
import TableIndex from '../pages/TableIndex'

//创建router实例对象，去管理一组一组的路由规则
const router = new VueRouter({
    mode:'hash',
    routes:[
        {
            path: '/',
            component: TableIndex
        },
    ]
})

//暴露router
export default router
