import ajax, {addQueryString_objectKey, addQueryString_real} from './ajax'
// import jsonp from 'jsonp'
import { jsonp } from 'vue-jsonp'
// let requestCurr = "/curr"
let requestCurr = ""

//话术入库搜索是否存在
export const setWordLibrary = (action,param) => {
    // console.log("param",param.search)
    const queryParam = addQueryString_real(param)
    // console.log("queryParam",queryParam)
    return ajax(requestCurr+'/setWordLibrary?action='+action,queryParam,'POST')
}

//导出话术
export const downWordWords = (param) => {
    // console.log("param",param.search)
    const queryParam = addQueryString_real(param)
    // console.log("queryParam",queryParam)
    return ajax(requestCurr+'/downWordWords?'+queryParam)
}


//添加常用话术
export const setThePhrase =  (action,param) => {
    // console.log("param",param.search)
    const queryParam = addQueryString_real(param)
    // console.log("queryParam",queryParam)
    return ajax(requestCurr+'/setThePhrase?action='+action,queryParam,'POST')
}





/**
 * 已知前后文 取中间文本
 * @param str 全文
 * @param start 前文
 * @param end 后文
 * @returns 中间文本 || null
 */
export function getStr(str, start, end) {
    let res = str.match(new RegExp(`${start}(.*?)${end}`))
    return res ? res[1] : null
}

