//能发生ajax请求异步的函数模块
//封装axios库
//函数的返回值是promise对象
import axios from 'axios'
import { Message } from 'element-ui'
// axios.default = IPCONFIG;//设置默认ip ipconfig这个参数。。
// console.log("axios.default",axios.default)
export default function ajax(url,data={},method='GET',timeout=10000) {

    return new Promise((resolve, reject)=>{
        let promise;
        if(method === 'GET'){ //GET请求方式
            promise =  axios.get(url,{ //配置对象
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                timeout: timeout, // 默认值是 `0` (永不超时)
                params: data  //指定的请求参数
            })
        }else{
            promise =  axios.post(url, data,{
                timeout:timeout,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                }
            })
        }

        promise.then(response =>{
            resolve(response.data)
        }).catch(error=>{
            // message.error("请求出错了"+error.message)
            // 请求出错了timeout of 10000ms exceeded
            if(error.message.indexOf('timeout') !== -1){ //超时了,返回对应的
                resolve({code:-101,msg:'超时了'})
            }else{
                Message({
                    showClose: true,
                    message: "请求出错了"+error.message,
                    type: 'error'
                });
            }


        })



    })


}

