<template>
  <div v-loading="divLoading">
    <div >
      <el-form
          :label-position="labelPosition"
          label-width="80px"
          :rules="rulesSearch"
          ref="searchData"
          :model="searchData"
          class="demo-form-inline"
          style="margin-left: 10px"
          :inline="true"
      >
        <el-form-item label="关键词"  prop="keyword" >
          <el-input
              v-model="searchData.keyword"
              type="textarea"
              @input="inputChange"
              :rows="2"
              placeholder="请输入搜索关键字"
              style="width: 300px"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left: 5px">
          <el-button type="primary" @click="searchLib('searchData')" icon="el-icon-search">关键字查询</el-button>
        </el-form-item>
        <el-form-item style="margin-left: 5px">
          <el-button type="primary" @click="showAddDrawe" icon="el-icon-plus" style="background-color: #f96868;border-color: #f96868;">话术入库</el-button>
        </el-form-item>
        <el-form-item>
          <el-button
              type="primary"
              icon="el-icon-upload2"
              style="background-color: #33cabb;border-color: #33cabb;"
              @click="commitUpload"
          >导入文件</el-button>
          <el-upload
              class="upload-demo"
              :action="uploadUrl"
              ref="fileRefs"
              :data="{'password':inputPassWord}"
              :before-upload="uploadBefore"
              :on-success="uploadSuccess"
              :on-error="uploadError"
              :show-file-list="false"
          >
          </el-upload>

        </el-form-item>
        <el-form-item style="margin-left: 5px">
          <el-button type="primary"  @click="exportFile" icon="el-icon-download" style="background-color: #926dde;border-color: #926dde;">导出文件</el-button>
        </el-form-item>
        <el-form-item style="margin-left: 5px">
          <el-button type="primary"  @click="addCommon" icon="el-icon-mouse" style="background-color: #8d6658;border-color: #8d6658;">查看常用话术</el-button>
        </el-form-item>
        <el-form-item style="margin-left: 5px">
          <el-dropdown>

            <el-button type="primary" style="border-color: #faa64b;background-color: #faa64b">
              当前语言({{localHanStr}})<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown" >
              <el-dropdown-item @click.native="langClick('zy')">中英</el-dropdown-item>
              <el-dropdown-item @click.native="langClick('zyy')">中英印</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>

        </el-form-item>
        <el-form-item v-if="phrase_username_show">
          <el-input
              v-model="username"
              placeholder="请输入名字"
          ></el-input>
        </el-form-item>
        <el-form-item v-if="phrase_username_show">
          <el-button type="primary"  @click="setUserName" icon="el-icon-setting" style="background-color: #f96197;border-color: #f96197;">设置名字</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div style="display: flex;flex-direction: column" v-if="normalShow">
      <div style="margin-left: 20px;color: blue;font-size: 24px;font-weight: bold;">
        话术详情-操作员【{{currUserName}}】
      </div>
      <div v-for="(item, index) in listData" :key="index" style="padding-bottom: 2px;padding-left: 20px;padding-right: 20px;padding-top: 2px" >
<!--        <el-skeleton animated />-->
        <el-row  >
          <el-col :span="2"  @click.native="ArticleDetail(index)">中文：</el-col>
          <el-col :span="10" style="white-space: pre-wrap;" @click.native="ArticleDetail(index)">{{item.Chinese}}</el-col>
          <el-col :span="2" >
            <el-button
                style="background-color: #926dde;border-color: #926dde;color:#ffffff"
                icon="el-icon-document-copy"
                size="mini"
                circle
                v-clipboard:copy="item.Chinese"
                v-clipboard:success="onCopy"
                v-clipboard:error="onError"
            ></el-button>
            <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                circle
                @click="delWordLib(item.ID)"
            ></el-button>
            <el-button

                icon="el-icon-star-off"
                size="mini"
                circle
                @click="collWordLib(item.ID)"
                v-if="item.CollectionStatus === 0"
            ></el-button>
            <el-button
                type="warning"
                icon="el-icon-star-on"
                size="mini"
                circle
                @click="collNot"
                v-if="item.CollectionStatus === 1"
            ></el-button>
          </el-col>

        </el-row>

        <div v-show="activeIndex === index">
          <el-row >
            <el-col :span="2">英文：</el-col>
            <el-col :span="10" style="white-space: pre-wrap;">{{item.English}}</el-col>
            <el-col :span="2">
              <el-button
                  style="background-color: #FF7941;border-color: #FF7941;color:#ffffff"
                  icon="el-icon-document-copy"
                  size="mini"
                  v-clipboard:copy="item.English"
                  v-clipboard:success="onCopy"
                  v-clipboard:error="onError"
              >复制英语</el-button>
            </el-col>
          </el-row>
          <el-row v-if="currLocalLangStr === 'zyy'">
            <el-col :span="2">印度文：</el-col>
            <el-col :span="2">
              <el-button
                  style="background-color: #33ABA0;border-color: #33ABA0;color:#ffffff"
                  icon="el-icon-document-copy"
                  size="mini"
                  v-clipboard:copy="item.India"
                  v-clipboard:success="onCopy"
                  v-clipboard:error="onError"
              >复制印地语</el-button>
            </el-col>
            <el-col :span="10" style="white-space: pre-wrap;">{{item.India}}</el-col>

          </el-row>
        </div>
        <div style="border-bottom:1px dashed #000000">

        </div>
      </div>
    </div>
    <div style="display: flex;flex-direction: column" v-if="!normalShow">
      <div style="margin-left: 20px;color: blue;font-size: 24px;font-weight: bold;">
        常用话术详情-操作员【{{currUserName}}】
      </div>
      <div v-for="(item, index) in parse_listData" :key="index" style="padding-bottom: 2px;padding-left: 20px;padding-right: 20px;padding-top: 2px" >
        <el-row  >
          <el-col :span="2"  @click.native="ArticleDetail(index)">中文：</el-col>
          <el-col :span="10" style="white-space: pre-wrap;" @click.native="ArticleDetail(index)">{{item.Chinese}}</el-col>
          <el-col :span="2" >
            <el-button
                style="background-color: #926dde;border-color: #926dde;color:#ffffff"
                icon="el-icon-document-copy"
                size="mini"
                circle
                v-clipboard:copy="item.Chinese"
                v-clipboard:success="onCopy"
                v-clipboard:error="onError"
            ></el-button>
            <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                circle
                @click="delWordLib(item.ID)"
            ></el-button>
          </el-col>
        </el-row>

        <div v-show="activeIndex === index">
          <el-row >
            <el-col :span="2">英文：</el-col>
            <el-col :span="10" style="white-space: pre-wrap;">{{item.English}}</el-col>
            <el-col :span="2">
              <el-button
                  style="background-color: #FF7941;border-color: #FF7941;color:#ffffff"
                  size="mini"
                  v-clipboard:copy="item.English"
                  v-clipboard:success="onCopy"
                  v-clipboard:error="onError"
              >复制英语</el-button>
            </el-col>
          </el-row>
          <el-row v-if="currLocalLangStr === 'zyy'">
            <el-col :span="2">印度文：</el-col>
            <el-col :span="2">
              <el-button
                  style="background-color: #33ABA0;border-color: #33ABA0;color:#ffffff"
                  size="mini"
                  v-clipboard:copy="item.India"
                  v-clipboard:success="onCopy"
                  v-clipboard:error="onError"
              >复制印地语</el-button>
            </el-col>
            <el-col :span="10" style="white-space: pre-wrap;">{{item.India}}</el-col>

          </el-row>
        </div>
        <div style="border-bottom:1px dashed #000000">

        </div>
      </div>
    </div>
    <el-drawer
        title="话术表单"
        :visible.sync="drawer"
        :direction="direction"
        :before-close="handleClose">
          <el-card class="box-card" style="margin-right: 10px">
<!--            <div slot="header" class="clearfix">-->
<!--              <span>话术表单</span>-->
<!--            </div>-->
            <div>
              <el-form
                  :label-position="labelPosition"
                  label-width="80px"
                  :rules="rules"
                  ref="formData"
                  :model="formData"
                  class="demo-form-inline"
                  style="margin-left: 10px"
              >
                <el-form-item label="中文"  prop="chinese">
                  <el-input v-model="formData.chinese" type="textarea" :rows="3" placeholder="请输入中文"></el-input>
                </el-form-item>
                <el-form-item label="英文" prop="english">
                  <el-input v-model="formData.english" type="textarea" :rows="3" placeholder="请输入英文" ></el-input>
                </el-form-item>
                <el-form-item label="印度语" prop="india">
                  <el-input v-model="formData.india" type="textarea" :rows="3" placeholder="请输入印度语" ></el-input>
                </el-form-item>
                <el-form-item label="入库密码" prop="passwordAdd" v-if="passwordViewShow">
                  <el-input v-model="formData.passwordAdd" placeholder="请输入密码" ></el-input>
                </el-form-item>
                <el-form-item style="margin-left: 5px">
                  <el-button type="primary" @click="addLib('formData')" icon="el-icon-check">{{ confirmAddLan }}</el-button>
                </el-form-item>
              </el-form>
            </div>
          </el-card>
    </el-drawer>
  </div>
</template>

<script>
import {parseTime,IMG_PATH} from "../utils/constant"
let Base64 = require('js-base64').Base64
import {
  setWordLibrary,
  downWordWords, setThePhrase
} from "../api";

export default {
  name: "TableAdmin",
  // components:{
  //   ChangePrice
  // },
  filters:{
    parseTime(data){
      // console.log("data",parseTime(data))
      return parseTime(data,'{y}-{m}-{d} {h}:{i}')
    }
  },
  data(){
    return{
      inputPassWord:null,
      divLoading:false,
      labelPosition: 'right',
      formData:{
        chinese:null,
        english:null,
        india:null,
        passwordAdd:null
      },
      searchData:{
        keyword:null,
      },
      rules:{
        chinese: [
          { required: true, message: '不允许为空,请填入中文', trigger: 'change' }
        ],
        english: [
          { required: true, message: '不允许为空,请填入英文', trigger: 'change' }
        ],
        // india: [
        //   { required: true, message: '不允许为空,请填入印度语', trigger: 'change' }
        // ],
        passwordAdd: [
          { required: true, message: '不允许为空,请填入入库密码', trigger: 'change' }
        ],
      },
      rulesSearch:{
        keyword: [
          { required: true, message: '不允许为空,请填入关键词', trigger: 'change' }
        ],
      },
      listData:[],
      parse_listData:[],
      drawer:false,
      direction: 'rtl',
      activeIndex: -1, // 初始化点击的索引值

      uploadUrl:'',
      confirmAddLan:'确认入库',
      phrase_username_show:true,
      passwordViewShow:true,
      username:'',
      currUserName:"未知",
      normalShow:true,
      localHanStr:'中英印'
    }
  },
  methods:{
    showAddDrawe(){
      // this.$refs['formData'].resetFields();
      this.direction = "rtl"
      this.drawer = true
      this.confirmAddLan = "确认入库"
      this.passwordViewShow = true
    },
    commitUpload(){

      this.$confirm("此操作将导入excel文件, 是否继续?", "危险操作", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async() => {


        this.$prompt('请输入密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputErrorMessage: '密码不能为空',
          inputValidator:(val) =>{
            if(val){
              return true;
            }else{
              return false;
            }
          },
        }).then(async ({ value }) => {

          this.inputPassWord = value
          this.$refs['fileRefs'].$refs['upload-inner'].handleClick()

        }).catch(() => {
          this.$notify({
            title: '提示',
            message: '取消导入操作',
            type: 'info'
          });
        });

      }).catch(() => {});





    },
    uploadBefore(){
      this.divLoading = true
    },
    uploadSuccess(response,file,fileList){
      this.divLoading = false
      // console.log("response,file,fileList",response,file,fileList)
      if(response.code === 200){
        this.$notify({
          title: '成功',
          message: response.msg,
          type: 'success'
        });
      }else{
        this.$notify.error({
          title: '错误',
          message: response.msg
        });
      }
    },
    uploadError(err,file,fileList){
      this.divLoading = false
      // console.log("err,file,fileList",err,file,fileList)
      this.$notify.error({
        title: '错误',
        message: err.msg
      });
    },
    exportFile(){//导出文件,后台生成

      this.$confirm("此操作将导出excel文件, 是否继续?", "危险操作", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async() => {

        this.$prompt('请输入密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputErrorMessage: '密码不能为空',
          inputValidator:(val) =>{
            if(val){
              return true;
            }else{
              return false;
            }
          },
        }).then(async ({ value }) => {

          let resultRes = await downWordWords({password:value})
          if(resultRes.code === 200){
            let downLink = resultRes.result
            if(downLink){
              // static/
              window.open("../../"+downLink)
            }
          }else{
            this.$notify({
              title: '错误',
              message: resultRes.msg,
              type: 'error'
            });
          }
        }).catch(() => {
          this.$notify({
            title: '提示',
            message: '取消导出操作',
            type: 'info'
          });
        });



      }).catch(() => {});



    },
    exportFile_web(){ //导出文件, //纯前端生成方式

      this.$confirm("此操作将导出excel文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        // this.excelData = this.dataList; //你要导出的数据list。
        this.export2Excel();
      })
      .catch(() => {});

    },
    export2Excel() {
      var that = this;
      require.ensure([], () => {
        const { export_json_to_excel } = require("../excal/Export2Excel"); //这里必须使用绝对路径，根据自己的命名和路径
        const tHeader = [
          "chinese",
          "english",
          "india",
        ]; // 导出的表头名
        const filterVal = [
          "Chinese",
          "English",
          "India",
        ]; // 导出的表头字段名
        const list = that.listData;
        // that.excelData为传入的数据
        const data = that.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, `话术导出excel`); // 导出的表格名称，根据需要自己命名
        // tHeader为导出Excel表头名称，`xxxxxx`即为导出Excel名称
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    async addLib(formName){ //确认添加按钮

      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.direction === "rtl"){
            this.handleSetWord()
          }else if(this.direction === "ltr"){
            // console.log("123123")
            this.handleSetWordLan()
          }
        } else {
          this.$notify({
            title: '错误',
            message: '无法查询,查询内容没通过校验',
            type: 'error'
          });
          return false;
        }
      });

    },
    addCommon(){//添加常用话术点击事件
      this.normalShow = false
      // this.direction = "ltr"
      // this.drawer = true
      // this.confirmAddLan = "确认添加常用话术"
      // this.passwordViewShow = false

      if(!this.currUserName || this.currUserName === '未知'){

      }else{
        this.handleGetWordLan()
      }

    },
    setUserName(){ //设置用户名,保存到本地
      if(!this.username || this.username === '' ){
        this.$notify({
          title: '错误',
          message: '用户名不能为空!',
          type: 'error'
        });
        this.phrase_username_show = true
      }else{

        if( this.username === '未知'){

          this.$notify({
            title: '错误',
            message: '不能设置用户名为未知!',
            type: 'error'
          });

          return false
        }

        this.$notify({
          title: '成功',
          message: "用户名设置成功！",
          type: 'success'
        });
        localStorage.setItem('phrase_username', this.username)
        this.currUserName = this.username
        this.username = ""
        this.phrase_username_show = false
        this.handleGetWordLan()
      }
    },
    async inputChange(){
      if(this.searchData.keyword){
        this.normalShow = true
        this.listData = []
        let requestData = {}
        this.currUserName = localStorage.getItem('phrase_username')
        if(!this.currUserName || this.currUserName === '未知'){

        }else{
          requestData.username = this.currUserName
        }

        requestData.search = encodeURIComponent(this.searchData.keyword)
        let result = await setWordLibrary('GET',requestData)
        // console.log("result",result)
        if(result.code === 200){
          if(result.result.length === 0){

            return false
          }
          this.listData = result.result
          // console.log("listData",this.listData)
        }else{
          this.$notify({
            title: '错误',
            message: result.msg,
            type: 'error'
          });
        }
      }
    },
    searchLib(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.handleGetWord()
        } else {
          this.$notify({
            title: '错误',
            message: '无法查询,查询内容没通过校验',
            type: 'error'
          });
          return false;
        }
      });
    },
    async handleGetWord(){
      this.normalShow = true
      this.divLoading = true
      this.listData = []
      let requestData = {}
      this.currUserName = localStorage.getItem('phrase_username')
      if(!this.currUserName || this.currUserName === '未知'){

      }else{
        requestData.username = this.currUserName
      }

      requestData.search = encodeURIComponent(this.searchData.keyword)
      let result = await setWordLibrary('GET',requestData)
      this.divLoading = false
      // console.log("result",result)
      if(result.code === 200){
        if(result.result.length === 0){
          this.$notify({
            title: '成功',
            message: "查询关键词内容为空,请确保话术是否存在库内或者查询的关键词是否正确！",
            type: 'warning'
          });
          return false
        }
        this.listData = result.result
        // console.log("listData",this.listData)
      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    async handleSetWord(){  //添加事件,话术入库
      let requestData = {}
      requestData.password = this.formData.passwordAdd
      requestData.chinese = encodeURIComponent(this.formData.chinese)
      // requestData.chinese = encodeURIComponent(Base64.encode(this.formData.chinese))
      requestData.english = encodeURIComponent(this.formData.english)
      requestData.india = encodeURIComponent(this.formData.india)
      let result = await setWordLibrary('ADD',requestData)
      // console.log("result",result)
      if(result.code === 200){
        this.$notify({
          title: '成功',
          message: result.msg,
          type: 'success'
        });

        this.$refs['formData'].resetFields();
      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    async handleSetWordLan(){ //确认添加常用话术
      if(!this.currUserName || this.currUserName === '未知'){
        this.$notify({
          title: '错误',
          message: '请先设置用户名,否则无法使用收藏功能!',
          type: 'error'
        });

        return false
      }
      let requestData = {}
      requestData.username = this.currUserName
      requestData.chinese = encodeURIComponent(this.formData.chinese)
      requestData.english = encodeURIComponent(this.formData.english)
      requestData.india = encodeURIComponent(this.formData.india)
      let result = await setThePhrase('ADD',requestData)
      // console.log("result",result)
      if(result.code === 200){
        this.$notify({
          title: '成功',
          message: result.msg,
          type: 'success'
        });

        this.$refs['formData'].resetFields();
        this.handleGetWordLan()
      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    async handleGetWordLan(){ //获取常用话术方法
      this.parse_listData = []
      let requestData = {}
      requestData.username = this.currUserName
      let result = await setThePhrase('GET',requestData)
      // console.log("result",result)
      if(result.code === 200){
        this.parse_listData = result.result
        // console.log("listData",this.listData)
      }else{

      }
    },
    onCopy(e) {
      this.$notify({
        title: '成功',
        message: '已复制到剪贴板',
        type: 'success'
      });
    },
    onError(e) {
      this.$notify({
        title: '错误',
        message: "复制失败",
        type: 'error'
      });
    },
    handleClose(){
      this.drawer = false
      this.$refs['formData'].resetFields();
    },
    ArticleDetail(index) {
      // this.activeIndex = index;
      // console.log("进来咯")
      this.activeIndex = this.activeIndex == index ? -1 : index;
    },
    collNot(){ //收藏过的点击事件
      this.$notify({
        title: '错误',
        message: '当前话术已收藏,无需重复收藏!',
        type: 'error'
      });
    },
    async collWordLib(indexId){  //收藏常用话术
      if(!this.currUserName || this.currUserName === '未知'){
        this.$notify({
          title: '错误',
          message: '请先设置用户名,否则无法使用收藏功能!',
          type: 'error'
        });

        return false
      }
      let requestData = {}
      requestData.username = this.currUserName
      requestData.id = indexId
      let result = await setWordLibrary('collection',requestData)
      // console.log("result",result)
      if(result.code === 200){
        this.$notify({
          title: '成功',
          message: result.msg,
          type: 'success'
        });
        this.handleGetWord()
        // this.$refs['formData'].resetFields();

      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    delWordLib(indexId){

      if(!this.normalShow){ //常用话术
        this.handleDeleteWordLan(indexId)
      }else{//普通话术
        this.$prompt('请输入删除密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputErrorMessage: '密码不能为空',
          inputValidator:(val) =>{
            if(val){
              return true;
            }else{
              return false;
            }
          },
        }).then(({ value }) => {

          this.handleDeleteWord(indexId,value)
        }).catch(() => {
          this.$notify({
            title: '提示',
            message: '取消删除操作',
            type: 'info'
          });
        });
      }



    },
    async handleDeleteWord(indexId,value){
      let requestData = {}
      requestData.id = indexId
      requestData.password = value
      let result = await setWordLibrary('DEL',requestData)
      if(result.code === 200){
        this.$notify({
          title: '成功',
          message: result.msg,
          type: 'success'
        });

        this.handleGetWord()

      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    async handleDeleteWordLan(indexId){
      let requestData = {}
      requestData.id = indexId
      let result = await setThePhrase('DEL',requestData)
      if(result.code === 200){
        this.$notify({
          title: '成功',
          message: result.msg,
          type: 'success'
        });

        this.handleGetWordLan()

      }else{
        this.$notify({
          title: '错误',
          message: result.msg,
          type: 'error'
        });
      }
    },
    langClick(valueStr){
      if(valueStr === 'zy'){
        localStorage.setItem('localLangStr', 'zy')

        this.localHanStr = '中英'
      }else if(valueStr === 'zyy'){
        localStorage.setItem('localLangStr', 'zyy')
        this.localHanStr = '中英印'
      }
      this.currLocalLangStr = localStorage.getItem('localLangStr')
    }
  },
  created() {
    this.uploadUrl = IMG_PATH
    // console.log("常用话术名",localStorage.getItem('phrase_username'))
    if(localStorage.getItem('phrase_username')){
        this.phrase_username_show = false
        this.currUserName = localStorage.getItem('phrase_username')
        this.currLocalLangStr = localStorage.getItem('localLangStr')


        if(this.currLocalLangStr === 'zyy'){
          this.localHanStr = '中英印'
        }else{
          this.localHanStr = '中英'
        }

        // if(!this.currUserName || this.currUserName === '未知'){
        //
        // }else{
        //   this.handleGetWordLan()
        // }

    }else{
        this.phrase_username_show = true
    }
  },
  computed:{

  },
  mounted() {

  },
  beforeDestroy() { //vue销毁之前

  }
}
</script>

