}
/*打开时文字位置设置*/
.switchStyle_input .el-switch__label--right {
  z-index: 1;
}
/* 调整打开时文字的显示位子 */
.switchStyle_input .el-switch__label--right span{
  /*margin-right: 9px;*/
  margin-left: 17px;
}
/*关闭时文字位置设置*/
.switchStyle_input .el-switch__label--left {
  z-index: 1;
}
/* 调整关闭时文字的显示位子 */
.switchStyle_input .el-switch__label--left span{
  margin-left: 25px;
}
/*显示文字*/
.switchStyle_input .el-switch__label.is-active {
